@import 'common/styles/scss/variables';

.App {
  text-align: center;
  background-color: #f5f5f5;
}

.App {
  > div {
    &:nth-child(n) {
      background-color: $bgPrimary;
    }

    &:nth-child(2n) {
      background-color: $bgSecondary;
    }
  }
}

